<template>
  <div id="app">
    <div class="container-fluid">
      <Header school='东北大学'></Header>
      <ProjectTitle></ProjectTitle>
      <TeamTab :data="tabData" :active-index="1" :type="1" v-on:save-team="saveTeam"></TeamTab>
      <div class="container team-container">
        <div class="bar team-title">
          <div class="logo"></div>
          <div class="name"><span>{{teamInfo.teamName}}</span></div>
          <el-button class="pull-right mt-4" @click="deleteTeam">解散团队</el-button>
<!--          <div class="set" @click="centerDialogVisible = true"><i class="iconfont icongerenshouye-shezhi1"></i></div>-->
        </div>
        <div class="main">
          <div class="apply-title">
            <div class="vertical"></div>
            <div class="title">成员列表</div>
            <div class="line"></div>
          </div>
          <div class="member-list">
            <div class="box member-box" v-for="obj in teamInfo.memberList" v-bind:key="obj.userCode">
              <div class="model"></div>
              <div class="action">
                <i class="iconfont iconjujue"></i>
                <span>移出团队</span>
              </div>
              <div class="name">{{obj.userName}}</div>
              <div class="position">{{obj.teamRole === 'leader' ? '队长' : '队员'}}</div>
              <div class="head-portrait"></div>
            </div>
            <div class="box add-box">
              <i class="iconfont iconyaoqinghaoyou"></i>
              <span>邀请加入</span>
            </div>
          </div>
        </div>
        <div class="apply">
          <div class="apply-title">
            <div class="vertical"></div>
            <div class="title">申请列表</div>
            <div class="line"></div>
          </div>
          <div class="member-list apply-list">
            <div class="box member-box apply-box" v-for="obj in teamInfo.applyList" v-bind:key="obj.userCode">
              <div class="action">
                <i class="iconfont iconduihao" @click="agreeMember(obj.userCode)"></i>
                <i class="iconfont iconjujue" @click="rejectMember(obj.userCode)"></i>
              </div>
              <div class="name">{{obj.userName}}</div>
<!--              <div class="time">申请时间：2019.07.08</div>-->
              <div class="head-portrait"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <el-dialog
      :visible.sync="deleteDialogVisible"
      width="468px"
      center>
      <div class="center-block tip-content">
        是否确定解散队伍
      </div>
      <span slot="footer" class="dialog-footer">
         <el-button style="width:140px;" type="primary" @click="centerDialogVisible = false">确定</el-button>
        <el-button style="width:140px;" type="primary" @click="centerDialogVisible = false">取消</el-button>
      </span>
    </el-dialog>
    <el-dialog
      :visible.sync="confirmDialogVisible"
      title="是否确定完成组队？"
      width="468px"
      center>
      <div class="confirm-content">
        <div class="box member-box" v-for="obj in teamInfo.memberList" v-bind:key="obj.userCode">
          <div class="name">{{obj.userName}}</div>
          <div class="position">{{obj.teamRole === 'leader' ? '队长' : '队员'}}</div>
          <div class="head-portrait"></div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
         <el-button style="width:140px;" type="primary" @click="completeTeam">确定</el-button>
        <el-button style="width:140px;" type="primary" @click="confirmDialogVisible = false">取消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Header from '../../../../components/school-training/Header.vue'
import ProjectTitle from '../../../../components/school-training/ProjectTitle.vue'
import TeamTab from '../../../../components/school-training/TeamTab.vue'
export default {
  components: {
    Header,
    ProjectTitle,
    TeamTab
  },
  name: 'app',
  mounted () {
    this.loadData()
  },
  data () {
    return {
      form: {
        name: '某某某的团队'
      },
      centerDialogVisible: false,
      deleteDialogVisible: false,
      confirmDialogVisible: false,
      searchKey: '',
      tabData: [{
        name: '选择项目',
        url: 'course-detail.html'
      }, {
        name: '组队',
        url: 'team-search.html'
      }, {
        name: '任务裁剪',
        url: ''
      }, {
        name: '人员管理',
        url: ''
      }],
      teamData: [],
      teamInfo: null
    }
  },
  methods: {
    loadData () {
      this.loadTeamList()
    },
    loadTeamList () {
      let me = this
      this.$http.post('/teambuild/queryteaminfoforleader', {
      }, res => {
        if (res.success) {
          me.teamInfo = res.data
        }
      })
    },
    deleteTeam () {
      let me = this
      // this.deleteDialogVisible = true
      this.$confirm('是否确定解散队伍?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$http.post('/teambuild/dismissteam', {
          teamId: me.teamInfo.teamId
        }, res => {
          if (res.success) {
            this.$message('已删除')
            window.location = 'team-search.html'
          }
        })
      })
    },
    saveTeam () {
      this.confirmDialogVisible = true
    },
    agreeMember (userCode) {
      let me = this
      this.$http.post('/teambuild/agreerefuseinvitecaptain', {
        teamId: me.teamInfo.teamId,
        joinStatus: 1,
        userCode: userCode
      }, res => {
        if (res.success) {
          me.$message('已同意成员申请')
          me.loadTeamList()
        }
      })
    },
    rejectMember (userCode) {
      let me = this
      this.$http.post('/teambuild/agreerefuseinvitecaptain', {
        teamId: me.teamInfo.teamId,
        joinStatus: -1,
        userCode: userCode
      }, res => {
        if (res.success) {
          me.$message('已拒绝成员申请')
          me.loadTeamList()
        }
      })
    },
    completeTeam () {
      window.location = 'project-task-review.html'
    }
  }
}
</script>

